

























import Vue from 'vue';

export default Vue.extend({
  props: {
    flexed: Boolean
  },
  computed: {
    LandlordID(): string {
      return this.$store.state.authentication.user.landlord_id;
    },
    PropertyID(): string {
      return this.$store.state.authentication.user.property_id;
    },
    TenantID(): string {
      return this.$store.state.authentication.user.tenant_id;
    }
  }
});
